import React, { useEffect, useRef, useState } from 'react'
import { Circle, HStack, Icon, useDisclosure, LinkBox, LinkOverlay } from "@chakra-ui/react"
import { IoVolumeMedium, IoVolumeOff } from "react-icons/io5";
import { Howl } from 'howler';
import underwater from './underwater.mp3'

import Box from '../components/Box';
import Flex from '../components/Flex';
import Link from '../components/Link';
import BackgroundImage from '../components/BackgroundImage';
import { Media, responsive } from '../contexts/responsive';

import Menu from './Menu';

// import logo from './logo.png';
import logo from './logo.svg';
import logo_black from './logo_black.svg';
import logo_text from './logo_text.svg';
import logo_text_black from './logo_text_black.svg';
import IconMenu from './HomePage/IconMenu';

const BackgroundAudio = (props) => {
  const [isPlaying, setPlaying] = useState(true)
  const howlerRef = useRef()
  useEffect(() => {
    howlerRef.current = new Howl({
      src: [underwater],
      loop: true,
      // autoplay: true,
      volume: 0.5,
    });
  }, [])
  useEffect(() => {
    if (howlerRef.current) {
      if (isPlaying) {
        howlerRef.current.play()
      } else {
        howlerRef.current.pause()
      }
    }
  }, [isPlaying])
  return (
    <Icon
      as={isPlaying ? IoVolumeMedium : IoVolumeOff}
      onClick={() => setPlaying(p => !p)}
      {...props}
    />
  )
}

const Header = ({ siteTitle, color, ...props }) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <Flex
      as="header"
      position="fixed"
      px={responsive('1em', '3.375em')}
      pt={responsive('0', '2.75em')}
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent="space-between"
      zIndex="docked"
      color={color}
      {...props}
    >
      <LinkBox
        as={Flex}
        cursor={'pointer'}
        width={responsive('32%', '35%')}
        alignItems="center"
      >
        <BackgroundImage width={responsive('100%', '37%')} src={color === 'black' ? logo_black : logo} ratio={202 / 45} />
        <Box flex={1} as={Media} greaterThanOrEqual='md'>
          <BackgroundImage src={color === 'black' ? logo_text_black : logo_text} ratio={344 / 45} />
        </Box>
        <LinkOverlay as={Link} to="/#" />
      </LinkBox>
      <HStack>
        <Circle cursor={'pointer'} border={'1px solid'} p={responsive('0.25em', '0.625em')}>
          <BackgroundAudio w={responsive(7, 10)} h={responsive(7, 10)} />
        </Circle>
        <Circle cursor={'pointer'} border={'1px solid'} p={responsive('0.375em', '0.875em')} onClick={onOpen}>
          <Icon as={IconMenu} w={responsive(6, 8)} h={responsive(6, 8)} />
        </Circle>
      </HStack>
      <Menu isOpen={isOpen} onClose={onClose} />
    </Flex>
  )
}

export default Header
