import React from 'react';
import { Button as ChButton } from "@chakra-ui/react";

import Link from './Link'

const Button = ({ href, to, ...props }) => {
  if (href || to) return <ChButton as={p => <Link {...p} to={to} href={href} />} {...props} />
  return <ChButton {...props} />
}

Button.defaultProps = {
  color: 'white',
  bg: 'transparent',
  border: '2px solid white',
  borderRadius: 'full',
  height: 'auto',
  width: 'auto',
  py: '0.5rem',
  px: '2.25rem',
  fontWeight: '500'
}

Button.Secondary = props => (
  <Button colorScheme="green" {...props} />
)

Button.Danger = props => (
  <Button colorScheme="red" {...props} />
)

Button.Outline = props => (
  <Button
    _hover={{
      bg: 'white',
      color: 'black'
    }}
    _active={{
      bg: 'white',
      color: 'black'
    }}
    {...props}
  />
)

Button.Transparent = props => (
  <Button
    bg="transparent"
    color="custom.text"
    border="none"
    _hover={{ bg: 'transparent' }}
    _active={{ bg: 'transparent' }}
    {...props}
  />
)

export default Button
